import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import AppBar from '@/components/app-bar';
import { map } from 'rxjs/operators';
import {
    Box,
    ButtonGroup,
    Button,
    CircularProgress,
    Container,
    FormControl,
    RadioGroup,
    FormControlLabel,
    Radio
} from '@material-ui/core';
import {
    fetchUsersList$
} from '@/api';
import useStyles from './styles';

interface Type {
    [index: string]: string;
}

const Type: Type = {
    0: '',
    1: '',
    2: ''
};
const FetchFunc = [fetchUsersList$];
const mapStateToProps = (state: any, ownProps: any) => {
    return {
        ...state.reviewDetailStore
    }
};
const mapDispatchToProps = (dispatch: any, ownProps: any) => {
    const { type } = ownProps.match.params;
    return {
        saveReviewDetail(info: object) {
            return dispatch({ type: 'SAVE_REVIEW_DETAIL', payload: info})
        }
    }
}
const CommonSelectList: React.FC = (props: any) => {
    const classes = useStyles();
    const [page, setPage] = useState(1);
    const [itemValue, setItemValue] = useState(props.value);
    const [next, setNext] = React.useState(null);
    const [list, setList] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [searchValue, setSearchValue] = useState('');
    const { type } = props.match.params;
    const Label = Type[type];

    const loadMore = () => {
        if (next) {
            setPage(page + 1);
        }
    };
    const handleSelectItem = (e: any) => {
        setItemValue(e.target.value);
    };
    const handleConfirm = () => {
        if(!itemValue) return;
        const item: any = list.find((item: any) => item.id === itemValue);
        props.saveReviewDetail({
            assesser: {
                label: item && item.username,
                value: itemValue
            }
        });
        props.history.goBack();
    };

    useEffect(() => {
        setLoading(true);
        const fetchFunc = FetchFunc[type];
        const Params: any = {
            page,
            page_size: 5
        };
        if(searchValue) {
            Params.search = searchValue;
        }
        const ApiInst = fetchFunc(Params).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next]),
            map(([results, next]) => [results.map((item: any) => ({...item, id: String(item.id)})), next])
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setList(list.concat(data[0]));
                setNext(data[1]);
                setLoading(false);
            }
        );
        return  () => ApiInst.unsubscribe();
    }, [page, searchValue]);
    return (
        <Box>
            <AppBar
                title={Label}
                // useSearch
                // searchPlaceholder=""
                // getSearchValue={setSearchValue}
            />
            <Container className={classes.contantContainer} >
                <FormControl component="fieldset">
                    <RadioGroup
                        aria-label="position"
                        name="position"
                        value={itemValue}
                        onChange={handleSelectItem}
                    >
                        {
                            list.map((item: any) => (
                                <FormControlLabel
                                    key={item.id}
                                    value={item.id}
                                    control={<Radio color="primary" />}
                                    label={item.username}
                                    labelPlacement="end"
                                />
                            ))
                        }
                    </RadioGroup>
                </FormControl>
            </Container>
            <Choose>
                <When condition={loading}>
                    <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                        <CircularProgress size={15} className={classes.circularProgress} />...
                    </Box>
                </When>
                <Otherwise>
                    <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                        {next ? '' : ''}
                    </Box>
                </Otherwise>
            </Choose>
            <Box position="fixed" left={15} right={15} bottom={15}>
                <ButtonGroup
                    fullWidth
                    size="medium"
                    variant="contained"
                    color="primary"
                    aria-label=""
                    onClick={() => handleConfirm()}
                >
                    <Button>
                        
                    </Button>
                </ButtonGroup>
            </Box>
        </Box>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(CommonSelectList);