const mapStateToProps = (state: any) => {
    return {
        ...state.reviewDetailStore
    }
};
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveReviewDetail(info: object) {
            return dispatch({
                type: 'SAVE_REVIEW_DETAIL',
                payload: info
            });
        }
    }
};
const ReviewDetail: React.FC = (props: any) => {
    const [assessDate, setAssessDate] = React.useState<any>(
        DateTime.local()
    );
    const [note, setNote] = React.useState('');
    const [imgFiles, setImgFiles] = React.useState<any>([]);
    const [cowBreastPosition, setCowBreastPosition] = React.useState<CowBreastPositionState>({
        lf: '',
        lb: '',
        rf: '',
        rb: ''
    });
    const [cowTypeAndResult, setCowTypeAndResult] = React.useState<CowTypeAndResult>({
        type: '',
        result: ''
    });
    const { ranchType, reviewType } = props.match.params;
    
    const LabelText: string = `${ReviewType[reviewType]}-`;
    const ResultsList = +reviewType === 2 ? LocomotionScore : Results;
    const classes = useStyles();

    function handleDateChange(date: any) {
        setAssessDate(date);
    }
    const handleCowBreastPosition = (name: keyof CowBreastPositionState) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setCowBreastPosition({...cowBreastPosition, [name]: event.target.value});
    };
    const handleCowTypeAndResult = (name: keyof CowTypeAndResult) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setCowTypeAndResult({...cowTypeAndResult, [name]: event.target.value});
    };
    const handleSubmit = () => {
        const formdata = new FormData();
        formdata.append('imgs', imgFiles[0]);
        uploadImgs$(formdata)
            .subscribe(
                data => {
                    console.log('data', data)
                },
                error => {
                    console.log('error', error)
                }
            )
    };
    const handleReviewDetail = (name: string) => (e: any) => {
        props.saveReviewDetail({
            [name]: e.target.value
        })
    }
    return (
        <React.Fragment>
            <AppBar title={`${RanchType[ranchType]}-${ReviewType[reviewType]}`} />
            <Container className={classes.root}>
                <FormControl fullWidth>
                    <Input
                        value={props.assesser.label}
                        placeholder=""
                        onFocus={() => props.history.push('/commonSelectList/0')}
                        startAdornment={<InputAdornment className={classes.InputAdornment} position="start">:</InputAdornment>}
                    />
                </FormControl>
                <FormControl fullWidth margin="normal">
                    <MuiPickersUtilsProvider utils={LuxonUtils} locale="zh-cn">
                        <DatePicker
                            id="mui-pickers-date"
                            label=""
                            format="yyyy-MM-dd"
                            value={props.assessDate}
                            onChange={date => props.saveReviewDetail({assessDate: date})}
                            okLabel=""
                            cancelLabel=""
                        />
                    </MuiPickersUtilsProvider>
                </FormControl>
                <TextAreaAndUploadImg
                    // getImgFiles={photo => props.saveReviewDetail({photo})}
                    // getTextAreaValue={note => props.saveReviewDetail({note})}
                    labelText={LabelText}
                    // note={props.note}
                    // photo={props.photo}
                />
                <Box>
                    <Box>
                        <FormControl margin="dense" fullWidth>
                            <Input
                                placeholder={`${ +reviewType !== 3 ? '' : ''}`}
                                startAdornment={
                                    (<InputAdornment className={classes.InputAdornment} position="start">
                                        { +reviewType !== 3 ? '' : ''}:
                                    </InputAdornment>)
                                }
                                endAdornment={
                                    (<If condition={+reviewType === 3}>
                                        <InputAdornment position="end">#</InputAdornment>
                                    </If>)
                                }
                            />
                        </FormControl>
                        <Choose>
                            <When condition={+reviewType !== 0}>
                                <FormControl margin="dense" fullWidth>
                                    <TextField
                                        select
                                        InputProps={{
                                            startAdornment: <InputAdornment className={classes.InputAdornment} position="start">:</InputAdornment>,
                                        }}
                                        onChange={handleCowTypeAndResult('type')}
                                        value={cowTypeAndResult.type}
                                    >
                                        {CowType.map(option => (
                                            <MenuItem key={option.value} value={option.value}>
                                                {option.label}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </FormControl>
                                <FormControl margin="dense" fullWidth>
                                    <TextField
                                        select
                                        InputProps={{
                                            startAdornment: <InputAdornment className={classes.InputAdornment} position="start">:</InputAdornment>,
                                        }}
                                        onChange={handleCowTypeAndResult('result')}
                                        value={cowTypeAndResult.result}
                                    >
                                        {ResultsList.map((option: any) => (
                                            <MenuItem key={option.value} value={option.value}>
                                                {option.label}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </FormControl>
                            </When>
                            <Otherwise>
                                <Grid container spacing={1}>
                                    {
                                        CowBreastPosition.map((position: CowBreastPositionItem) => (
                                            <Grid item xs={6} key={position.key}>
                                                <FormControl margin="dense" fullWidth>
                                                    <TextField
                                                        select
                                                        InputProps={{
                                                            startAdornment: <InputAdornment className={classes.inputAdornment} position="start">{position.label + ':'}</InputAdornment>,
                                                        }}
                                                        onChange={handleCowBreastPosition(position.key)}
                                                        value={cowBreastPosition[position.key]}
                                                    >
                                                        {Teats.map(option => (
                                                            <MenuItem key={option.value} value={option.value}>
                                                                {option.label}
                                                            </MenuItem>
                                                        ))}
                                                    </TextField>
                                                </FormControl>
                                            </Grid>
                                        ))
                                    }
                                </Grid>
                            </Otherwise>
                        </Choose>
                        <Box className={classes.flexbox}>
                            <IconButton>
                                <AddCircleOutlineIcon color="primary" />
                            </IconButton>   
                        </Box>
                    </Box>
                </Box>
                <Box className={classes.btnContainer}>
                    <Box>n/</Box>
                    <FormControlLabel
                        className={classes.btnContainer}
                        control={
                            <Checkbox
                                // className={classes.btnContainer}
                                // checked={state.checkedB}
                                // onChange={handleChange('checkedB')}
                                icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                                checkedIcon={<CheckBoxIcon fontSize="small" />}
                                value="checkedB"
                                color="primary"
                            />
                        }
                        classes={{label: classes.btnContainer}}
                        label=""
                    />
                    <ButtonGroup
                        onClick={handleSubmit}
                        className={classes.btnMargin}
                        fullWidth
                        size="medium"
                        variant="contained"
                        color="primary"
                        aria-label="Full width outlined button group"
                    >
                        <Button>
                            
                        </Button>
                    </ButtonGroup>
                </Box>
            </Container>
        </React.Fragment>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(ReviewDetail);