
import { DateTime } from 'luxon';
import {
    SAVE_ASSESSER,
    SAVE_DATE,
    SAVE_NOTE,
    SAVE_IMGS,
    SAVE_COW_TYPE,
    SAVE_COW,
    SAVE_COWSHED,
    SAVE_TEATS,
    SAVE_RESULTS,
    SAVE_LOCOMOTION_SCORE,
    SAVE_REVIEW_DETAIL
} from '../actionTypes';

const initState = {
    assesser: {
        label: '',
        value: ''
    },
    assessDate: DateTime.local(),
    note: '',
    photo: [],
    cow: {
        label: '',
        value: ''
    },
    lf: '',
    lb: '',
    rf: '',
    rb: '',
    cowType: '',
    results: '',
    locomotionScore: ''
};

export default function (state = initState, action: any) {
    switch (action.type) {
        case SAVE_REVIEW_DETAIL: {
            return {
                ...state,
                ...action.payload
            }
        }
        default:
            return initState
    }
};