import { ajax } from 'rxjs/ajax';
import { baseUrl } from '@/config';
import { EMPTY } from 'rxjs';
import store from '@/redux/store';

export default {
    get(url: string, params: object, options?: ApiOptions) {
        let initUrl = `${baseUrl}${url}/`;
        let headers: ApiHeaders = {};
        let formaturl: string = '';
        let { token, farm_code: farmcode } = JSON.parse(localStorage.getItem('userInfo') || '{}');
        const globalState = store.getState();
        
        if (options && options.useToken) {
            if (token && globalState.userInfoStore.token) {
                headers.AUTHORIZATION = `JWT ${token}`;
            } else{
                store.dispatch({
                    type: 'SAVE_USER_INFO',
                    payload: {
                        userInfo: {
                            isLogin: false,
                            token: '',
                            farm_code: ''
                        }
                    }
                });
                window.location.href=`${window.location.origin}/#/login`;
                return EMPTY;
            }
        }
        if(options && options.useFarmCode) {
            if (!farmcode) {
                window.location.href=`${window.location.origin}/#/login`;
                return EMPTY;
            }
            initUrl = `${baseUrl}${farmcode}/${url}/`;
        }

        formaturl = `${initUrl}?${Object.entries(params).map(item => item.join('=')).join('&')}`;

        return ajax.get(formaturl, headers);
    },
    post(url: string, params: object, options?: ApiOptions) {
        let headers: ApiHeaders = {};
        let initUrl = `${baseUrl}${url}/`;
        const { token, farm_code: farmcode } = JSON.parse(localStorage.getItem('userInfo') || '{}');
        const globalState = store.getState();
        
        if (options && options.useToken) {
            if (token && globalState.userInfoStore.token) {
                headers.AUTHORIZATION = `JWT ${token}`;
            } else{
                store.dispatch({
                    type: 'SAVE_USER_INFO',
                    payload: {
                        userInfo: {
                            isLogin: false,
                            token: '',
                            farm_code: ''
                        }
                    }
                });
                window.location.href=`${window.location.origin}/#/login`;
                return EMPTY;
            }
        }
        if(options && options.useFarmCode) {
            if (!farmcode) {
                window.location.href=`${window.location.origin}/#/login`;
                return EMPTY;
            }
            initUrl = `${baseUrl}${farmcode}/${url}/`
        }
        if(options && options.overrideUrl) {
            initUrl = options.overrideUrl(baseUrl, url, { farmcode });
        }
        if(options && options.overrideHeaders) {
            headers = options.overrideHeaders(headers);
        }
        return ajax.post(initUrl, params, headers);
    }
}