import React, { useState, useEffect } from 'react';
import {connect} from 'react-redux';
import {
    Box,
    Grid,
    IconButton,
    TextField
} from '@material-ui/core';
import {
    AddAPhoto as AddAPhotoIcon,
    Delete as DeleteIcon
} from '@material-ui/icons'
import {useDropzone} from 'react-dropzone';
import {
    makeStyles
} from '@material-ui/styles';
import R from 'ramda';

const useStyles = makeStyles({
    flexbox: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    },
    img: {
        display: 'block',
        width: '100%',
        height: '90px'
    },
    deleteIcon: {
        color: 'red'
    }
});
interface IProps {
    labelText: string;
}
const mapStateToProps = (state: any) => {
    return {
        photo: state.reviewDetailStore.photo,
        note: state.reviewDetailStore.note
    }
};
const mapDispatchToProps = (dispatch: any) => {
    return {
        saveReviewDetail(info: object) {
            return dispatch({
                type: 'SAVE_REVIEW_DETAIL',
                payload: info
            });
        }
    }
};
const TextAreaAndUploadImg: React.FC<IProps> = (props: any) => {
    const classes = useStyles();
    // const [photo, setPhoto] = useState<any[]>(props.photo);
    const {getRootProps, getInputProps} = useDropzone({
        accept: 'image/*',
        onDrop: acceptedFiles => {
            const photoFormat: any = props.photo.concat(acceptedFiles.map(file => Object.assign(file, {
                preview: URL.createObjectURL(file)
            })));
            props.saveReviewDetail({
                photo: photoFormat
            });
            // setPhoto(photoFormat);
        }
    });
    const deleteImg = (i: number) => {
        const photoClone = R.remove(i, 1, props.photo);
        props.saveReviewDetail({
            photo: photoClone
        });
        // setPhoto(photoClone);
    }

    // useEffect(() => () => {
    //     props.photo.forEach((file: any) => URL.revokeObjectURL(file.preview));
    // }, [props.photo]);

    return (
        <Grid container>
            <Grid item xs>
                <TextField
                    id="outlined-textarea"
                    label={props.labelText}
                    multiline
                    margin="normal"
                    variant="outlined"
                    fullWidth
                    value={props.note}
                    onChange={e => props.saveReviewDetail({
                        note: e.target.value
                    })}
                />
            </Grid>
            <Grid item xs={2} className={classes.flexbox}>
                     <Box {...getRootProps({className: 'dropzone'})}>
                        <input {...getInputProps()} />
                        <IconButton>
                            <AddAPhotoIcon color="primary" />
                        </IconButton>
                    </Box>
            </Grid>
            <Grid container spacing={1} alignItems="center">
                {
                    props.photo.map((item: any, i: any) => (
                        <Grid item xs={4} key={`img-file-${i}`}>
                            <Box position="relative">
                                <img
                                    className={classes.img}
                                    src={item.preview}
                                    alt={`img-file-${i}`}
                                />
                                <Box position="absolute" top="-20px" right="-20px">
                                    <IconButton
                                        className={classes.deleteIcon}
                                        onClick={() => deleteImg(i)}
                                    >
                                        <DeleteIcon />
                                    </IconButton>
                                </Box>
                            </Box>
                        </Grid>
                    ))
                }
            </Grid>
        </Grid>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(TextAreaAndUploadImg);