import React from 'react';
import {
    Container,
    FormControl,
    TextField,
    InputAdornment,
    MenuItem,
    Input,
    Box,
    Grid,
    IconButton,
    ButtonGroup,
    Button,
    FormControlLabel,
    Checkbox
} from '@material-ui/core';
import {
    AddAPhoto as AddAPhotoIcon,
    AddCircleOutline as AddCircleOutlineIcon,
    CheckBox as CheckBoxIcon,
    CheckBoxOutlineBlank as CheckBoxOutlineBlankIcon
} from '@material-ui/icons';
import {
    makeStyles
} from '@material-ui/styles';
import {
    MuiPickersUtilsProvider,
    DatePicker,
} from '@material-ui/pickers';
import LuxonUtils from "@date-io/luxon";
import { DateTime } from 'luxon';
import AppBar from '@/components/app-bar';

interface TaskForm {
    type: string;
    cowshedNumber: string;
    people: string;
    mark: ''
}
const useStyles = makeStyles({
    root: {
        marginTop: 60,
        height: '100vh',
        overflowY: 'auto'
    },
    inputAdornment: {
        '& p': {
            width: 75
        }
    },
    flexbox: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    },
    btnMargin: {
        marginTop: 0
    },
    btnContainer: {
        fontSize: 12,
        lineHeight: 1,
        color: '#111'
    }
});

const TypeList = [
    {
        label: '',
        value: '1'
    },
    {
        label: '',
        value: '2'
    },
    {
        label: 'TMR',
        value: '3'
    },
    {
        label: '',
        value: '4'
    },
    {
        label: '',
        value: '5'
    },
    {
        label: '',
        value: '6'
    }
];
    
const RanchType: any = {
    ald: '',
    society: ''
};
const ViewCowshed: React.FC = (props: any) => {
    const { ranchType } = props.match.params;
    const TitleText: string = `${RanchType[ranchType]}-`;
    const [taskForm, setTaskForm] = React.useState<TaskForm>({
        type: '',
        cowshedNumber: '',
        people: '',
        mark: ''
    });
    const [date, setDate] = React.useState<any>(
        DateTime.local()
    );
    const classes = useStyles();
    const handleChange = (name: string) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setTaskForm({...taskForm, [name]: event.target.value});
    };
    const handleDateChange = (date: any) => setDate(date);

    return (
        <div>
            <AppBar title={TitleText} />
            <Container className={classes.root}>
                <FormControl margin="dense" fullWidth>
                    <TextField
                        select
                        InputProps={{
                            startAdornment: <InputAdornment className={classes.inputAdornment} position="start">:</InputAdornment>,
                        }}
                        onChange={handleChange('type')}
                        value={taskForm.type}
                    >
                        {TypeList.map(option => (
                            <MenuItem key={option.value} value={option.value}>
                                {option.label}
                            </MenuItem>
                        ))}
                    </TextField>
                </FormControl>
                <FormControl fullWidth margin="normal">
                    <MuiPickersUtilsProvider utils={LuxonUtils} locale="zh-cn">
                        <DatePicker
                            id="mui-pickers-date"
                            label=""
                            format="yyyy-MM-dd"
                            value={date}
                            onChange={handleDateChange}
                        />
                    </MuiPickersUtilsProvider>
                </FormControl>
                <Box>
                    <FormControl margin="dense" fullWidth>
                        <Input
                            onChange={handleChange('cowshedNumber')}
                            value={taskForm.cowshedNumber}
                            placeholder=""
                            startAdornment={<InputAdornment className={classes.inputAdornment} position="start">:</InputAdornment>}
                            endAdornment={<InputAdornment position="end" className={`${classes.inputAdornment} text-right`}>#</InputAdornment>}
                        />
                    </FormControl>
                    <FormControl margin="dense" fullWidth>
                        <Input
                            onChange={handleChange('people')}
                            value={taskForm.people}
                            placeholder=""
                            startAdornment={<InputAdornment className={classes.inputAdornment} position="start">:</InputAdornment>}
                        />
                    </FormControl>
                    <Grid container>
                        <Grid item xs>
                            <TextField
                                onChange={handleChange('mark')}
                                value={taskForm.mark}
                                id="outlined-textarea"
                                label=""
                                multiline
                                margin="normal"
                                variant="outlined"
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={2} className={classes.flexbox}>
                            <IconButton>
                                <AddAPhotoIcon color="primary" />
                            </IconButton>
                        </Grid>
                    </Grid>
                </Box>
                <Box className={classes.flexbox}>
                    <IconButton>
                        <AddCircleOutlineIcon color="primary" />
                    </IconButton>   
                </Box>
                <Box className={classes.btnContainer}>
                    <FormControlLabel
                        className={classes.btnContainer}
                        control={
                            <Checkbox
                                // className={classes.btnContainer}
                                // checked={state.checkedB}
                                onChange={handleChange('checkedB')}
                                icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                                checkedIcon={<CheckBoxIcon fontSize="small" />}
                                value="checkedB"
                                color="primary"
                            />
                        }
                        classes={{label: classes.btnContainer}}
                        label=""
                    />
                    <ButtonGroup
                        className={classes.btnMargin}
                        fullWidth
                        size="medium"
                        variant="contained"
                        color="primary"
                        aria-label="Full width outlined button group"
                    >
                        <Button>
                            
                        </Button>
                    </ButtonGroup>
                </Box>
            </Container>
        </div>
    )
}
export default ViewCowshed