import base from './base';

export const loginIn$ = (params: object) => base.post('jauth', params, { useToken: false });

export const fetchCanUseFarms$ = (params: object) => base.get('farms', params, { useToken: true });

export const uploadImgs$ = (params: object) => base.post('imagesupload', params, { useFarmCode: true, useToken: true });

// 
export const fetchTodoTaskRuns$ = (params: object) => base.get('todotaskruns', params, { useFarmCode: true, useToken: true });

// 
export const fetchUsersList$ = (params: object) => base.get('users', params, { useFarmCode: true, useToken: true });
