import { combineReducers } from 'redux';
import { createStore } from 'redux';
import userInfoStore from './reducers/index';
import reviewDetailStore from '@/views/review-detail/redux/reducers';
const rootReducers = combineReducers({ userInfoStore, reviewDetailStore });

export default createStore(
    rootReducers,
    (window as any).__REDUX_DEVTOOLS_EXTENSION__ && (window as any).__REDUX_DEVTOOLS_EXTENSION__()
);